import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { MatButtonModule } from '@angular/material/button';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { GreenSquareComponent, GreenSquareModule } from '@terrasoft/studio-enterprise/feature/green-square';

import { AppComponent } from './app.component';

@NgModule({
	declarations: [GreenSquareComponent],
	imports: [BrowserModule, BrowserAnimationsModule, MatButtonModule],
	providers: [],
	entryComponents: [GreenSquareComponent]
})
export class AppModule {
	constructor(public injector: Injector) {}

	ngDoBootstrap() {
		const greenSquare = createCustomElement(GreenSquareComponent, { injector: this.injector });
		customElements.define('ts-green-square', greenSquare);
	}
}
