import { Component } from '@angular/core';

@Component({
  selector: 'ts-green-square',
  templateUrl: './green-square.component.html',
  styleUrls: ['./green-square.component.scss']
})
export class GreenSquareComponent {

	public value = '';

	public symbols = ['t', 'e', 'c', 'h', ' ', 'p', 'o', 'i', 'n', 't'];

	public onNextButtonClick() {
		if (this.symbols.length === 0) {
			return;
		}

		this.value += this.symbols.shift();
	}

}
